import React from 'react';
import unsplash1 from "../images/unsplash1.jpg";

function Dashbord({selected, dashbordNo, newSelected}) {


  let topFiveSelected = selected.slice(0, 5);

 // topFiveSelected.filter( (index fil)=>{
  //  return index == id
  //})
 
  const handleRemove = (e)=>{
    let id = e.target.value
   // console.log(id);
    
    const delSelected = topFiveSelected.filter((index, fil)=> index === id)
    topFiveSelected.push(delSelected);

    console.log(delSelected);
  }

  if(dashbordNo === 0){
    return(
      <div className="card alert alert-warning">
        <p>please go to the home page to nominate 5 movies</p>
      </div>
    )
  }else if(dashbordNo === 1){
      return (
        topFiveSelected.map((movie, index) =>(
          <div key={index} className="movieDisplay">
            <div className="img-container">
              <img className="imgPoster" src={movie.Poster ==""?null:movie.Poster} alt=" poster not available"/>
            </div>
            <div className="movieInfo-container">
              <p className="title">
                <span>Title:</span>
                {movie? movie.Title:null}
                {movie? movie.Year:null}
              </p>
              <p>
                <span>Genre:</span>
                {movie? movie.Genre:null}
              </p>
              <p className="plot">
                <span>Plot:</span>
                {movie? movie.Plot:null}
              </p>
              {
              movie == ""?"":""}
              {movie.Title?(
                <button 
                value={index}
                onClick={handleRemove}
                className="btn btn-block">remove from nomination
                </button>
              ):""} 
            </div>  
          </div>
      ))
      )
  }else if(dashbordNo === 2){
    return(
      <div className="card alert alert-warning">
         page to nominate 5 movies
      </div>
    )
  }
}

export default Dashbord
