import React from 'react'
import Nomination from './Dashbord'
import unsplash1 from "../images/unsplash1.jpg";
import loading from "../images/loading.jpg";

function CurrentMovie({currentMovie, handleNominated,nominated,displaynumber,maxError}) {
  

  let btnStatus = nominated?"btn-success":"btn-primary";

  if(displaynumber === 0){
    return <div className="alert alert-info card">
    <p>welcome to Masko award show</p>
    <p>please nominate maximum of 5 movies</p>
    </div>
  }else if(displaynumber === 1){
    return(
        <div className="movieDisplay">
          <div className="img-container">
            <img className="imgPoster" src={currentMovie? currentMovie.Poster:unsplash1} alt=" poster not available"/>
          </div>
          <div className="movieInfo-container">
            <p className="title">
              <span>Title:</span> 
              {currentMovie? currentMovie.Title:null}
              {currentMovie? currentMovie.Year:null}
            </p>
            <p>
              <span>Genre:</span>
              {currentMovie? currentMovie.Genre:null}
            </p>
            <p className="plot">
              <span>Plot:</span>
              {currentMovie? currentMovie.Plot:null}
            </p>
            <button 
              className={`btn ${btnStatus} btn-block`} onClick={handleNominated}>
                {nominated?"Nominated search another movie.":"Nominate"}
            </button>
            <p className="aler alert-danger">
            {maxError?"Maximum nominated":""}
            </p>
          </div>
          
          
        </div>
    )
  }else if(displaynumber === 3){
    return <div className="alert alert-info card">no result search for a movie</div>
  }else if(displaynumber === 4){
    return <div className="alert alert-danger card">error</div>
  }else if(displaynumber === 5){
    return <div className="card"><img className="loading" src={loading} alt="loading.."/></div>
  }
}

export default CurrentMovie
