import React, { useState } from 'react'

function Search({handleSubmit}) {


  
  const [movieType, setMovieType] = useState("");
  const [movieTitle, setMovieTitle] = useState("");
  const [movieYear, setMovieYear] = useState("");

  const handleChange = (e)=>{
    if(e.target.name === "movieType"){
      setMovieType(e.target.value);
    }else if(e.target.name === "movieTitle"){
      setMovieTitle(e.target.value);
    }else if(e.target.name === "movieYear"){
      setMovieYear(e.target.value);
    }
  }
  return (
    <div className="search">
        <div className="form-group input-text">
          <input 
          required 
          className="form-control" 
          type="text" 
          placeholder="search title ..." 
          onChange={handleChange} 
          name="movieTitle" 
          value={movieTitle}
          />
          <input type="number" 
            className="form-control" 
            placeholder="Movie year..." 
            onChange={handleChange} 
            name="movieYear" 
            value={movieYear}
            />  
        </div>
        <div className="form-group">
          <select 
            className="form-control" 
            name="movieType" 
            onChange={handleChange}
            value={movieType}>
            <option>Select type</option>
            <option value="movie">movie</option>  
            <option value="series">series</option>
            <option value="episode">episode</option>
          </select>
        </div>
        <input 
        onClick={()=>handleSubmit(movieType,movieTitle,movieYear)}
        className="form-control btn btn-primary" type="submit" value="search"/>
      </div>
  )
}

export default Search
